import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SPost = styled.a`
  display: flex;
  flex-direction: column;
  gap: 0.625rem;

  width: 100%;
  max-width: 22.375rem;
  height: 16.313rem;

  border-radius: 1.063rem;

  overflow: hidden;
  position: relative;

  text-align: left;
  cursor: pointer;

  background-color: white;
  transition: 0.25s ease-in-out;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -10;
  }

  &:hover {
    transform: translateX(-2px) translateY(-5px);
  }
`;

const Img = styled.img`
  aspect-ratio: 521/380;
  height: 100%;
  width: fit-content;
  border-radius: 1.063rem;
`;

const Desc = styled.div`
  position: absolute;
  bottom: 0.5rem;
  left: 0;

  padding: 1.25rem;
  margin: 00.5rem;

  background: rgba(255, 255, 255, 0.21);
  border: 2px solid rgba(238, 247, 255, 0.25);

  backdrop-filter: blur(16.5px);
  border-radius: 0.75rem;

  display: flex;
  flex-direction: column;
  gap: 0.625rem;
`;

const Info = styled.div`
  display: flex;
  gap: 0.5rem;
  align-items: center;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
  color: #ffffff;

  opacity: 0.4;
`;

const Line = styled.div`
  width: 1px;
  height: 0.688rem;
  opacity: 0.3;
  background-color: #ffffff;
`;

const SText = styled(Text)`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 120%;
  color: #ffffff;
`;

const Post = ({ img, desc, href, date, type }) => {
  return (
    <SPost href={href} aria-label={"Прочитать статью"}>
      <Img src={img} alt="post_img" loading="lazy" />
      <Desc>
        <SText color="#3C3C3C" size={20}>
          {desc}
        </SText>
        <Info>
          {date} <Line /> {type === 1 ? "Кейсы" : "Клиенты"}
        </Info>
      </Desc>
    </SPost>
  );
};

export default Post;
