import styled from "styled-components";
import { useEffect, useRef } from "react";
import { useState } from "react";

import { Text, Title3 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonMobile";
import Post from "./postMobile";

import post_img_1 from "../../assets/images/Test/post_img_1.webp";
import post_img_2 from "../../assets/images/Test/post_img_2.webp";
import post_img_3 from "../../assets/images/Test/post_img_3.webp";

import { EngLoc } from "../../assets/constants/localizations";
import { smooth_up } from "../../assets/styles/animations";
import { Telegram } from "../../assets/images/Socials/Telegram";
import { YandexZen } from "../../assets/images/Socials/YandexZen";

const SPosts = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 3.75rem 0;
  gap: 2.188rem;
  margin: 0 1rem;

  @media (min-width: 600px) {
    margin: 0 4%;
  }
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: 2.375rem;
  line-height: 110%;

  background: linear-gradient(90.45deg, #116ad0 1.65%, #00aee8 114.47%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
`;

const PostsList = styled.div`
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  gap: 1.25rem;
  width: 100%;
  margin-bottom: 1.563rem;

  & {
    opacity: 0;
    ${({ view }) => !view && smooth_up}
  }
`;

const Description = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 2.313rem;

  width: 100%;

  & p {
    max-width: 267px;
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 150%;
    color: #000;
  }

  & button {
    height: 4rem;
    border-radius: 34.5px;
    font-weight: 400;
    font-size: 1.125rem;
    line-height: 1.375rem;
    border: 1px solid rgba(0, 174, 232, 0.37);
  }

  & > div {
    display: flex;
    gap: 0.938rem;
  }
`;

const Social = styled.div`
  width: 4rem;
  height: 4rem;

  border: 1px solid rgba(0, 174, 232, 0.37);
  border-radius: 34.5px;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const ListPosts = [
  {
    img: post_img_2,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 1,
  },
  {
    img: post_img_1,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 2,
  },
  {
    img: post_img_2,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 1,
  },
  {
    img: post_img_3,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 1,
  },
  {
    img: post_img_1,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 1,
  },
  {
    img: post_img_3,
    desc: "Как продать подписку за 6 шагов: схема прогрева в соцсетях",
    date: "18.10.2022",
    href: "#",
    type: 1,
  },
];

const Posts = ({ language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  return (
    <SPosts ref={elem}>
      <Title weight={300}>
        {language === "Ru" ? "Наши статьи" : EngLoc.posts.title}
      </Title>
      <PostsList view={view}>
        {ListPosts.map((post, index) => {
          return (
            <Post
              key={index}
              href={post.href}
              type={post.type}
              date={post.date}
              img={post.img}
              desc={post.desc}
            />
          );
          return null;
        })}
      </PostsList>
      <Description>
        <Text>
          Больше полезных статей о маркетинге в социальных сетях и мессенжерах
          читайте в блоге или в удобной социальной сети
        </Text>
        <div>
          <a
            href="https://blog.socialjet.pro/"
            aria-label={"Перейти в блог SocialJet"}
          >
            <Button
              width="100%"
              type="service"
              colorTxt="#00AEE8"
              color="#fff"
              link={true}
            >
              {language === "Ru" ? "Наш блог" : EngLoc.buttons.blog}
            </Button>
          </a>

          <Social>
            <Telegram width="24" height="24" color="#00AEE8" />
          </Social>
          <Social>
            <YandexZen width="24" height="24" color="#00AEE8" />
          </Social>
        </div>
      </Description>
    </SPosts>
  );
};

export default Posts;
